import './about.css'
import './pointer.css'

if(screen.width<900){
    mobile=1;
}else{
    mobile=0;
}

let imgClone;
let closeBtn;
let imgOverlay;
var imgFs = 0;

document.addEventListener('click', function(e) {
    e = e || window.event;
    var target = e.target;
    
    if(imgFs == 0 && target.tagName == 'IMG'){
    imgFs = 1;

    document.getElementById('html').style.overflowY='hidden'
    imgOverlay = document.createElement("div");
    imgOverlay.style.backgroundColor = "var(--main-black)"
    imgOverlay.style.opacity = "90%"
    imgOverlay.style.position = "fixed" 
    imgOverlay.style.width="100%"
    imgOverlay.style.height="100%"
    imgOverlay.style.left = "0px" 
    imgOverlay.style.top = "0px" 
    imgOverlay.style.zIndex = "10" 
    document.body.appendChild(imgOverlay);

    imgClone = target.cloneNode(true);

    imgClone.style.animation='swift-ptext 0.4s forwards ease-in-out 0s';
    imgClone.style.border = '0px'
    imgClone.style.position = "fixed" 
    imgClone.style.left = "var(--big-padding)" 
    imgClone.style.top = "var(--big-padding)" 
    imgClone.style.zIndex = "100" 
    imgClone.style.width = "calc(100% - calc(2 * var(--big-padding)))" 
    imgClone.style.height = "calc(100% - calc(4 * var(--big-padding)))" 
    imgClone.style.objectFit= "contain"
    document.body.appendChild(imgClone);

    closeBtn = document.createElement("h1");
    closeBtn.style.top= "var(--big-padding)"
    closeBtn.style.right= "var(--big-padding)"
    closeBtn.style.position = "fixed" 
    closeBtn.style.color = "var(--main-white)"
    closeBtn.style.mixBlendMode = "difference"
    closeBtn.style.transform = "translatey(0px)"
    closeBtn.innerHTML="X"
    closeBtn.style.zIndex = "101" 
    document.body.appendChild(closeBtn);

    }else if(imgFs==1){
        imgClone.remove();
        imgOverlay.remove();
        closeBtn.remove();
        imgFs=0;
        document.getElementById('html').style.overflowY='scroll'
    }
    

}, false);

init_pointer({
    pointerColor: "#000",
    ringSize: getRingSize(),
    ringClickSize: 10
  })

  
  function getRingSize(){
  if(mobile==0){
      return 25;
  }else{
      return 0;
  }
  }
  

  function createTag(name, card){
    const projectTag = document.createElement("div");
    projectTag.className = "projectTag";
    projectTag.innerHTML = name;
    card.appendChild(projectTag);
  }



//DOM STUFF BELOW
var words= []


window.onload = function() {

    words[0] = document.getElementById("footer1"); 
    checkVisibility();
    
   }


window.addEventListener('scroll', function (event) {
    words[0] = document.getElementById("footer1"); 
    checkVisibility()

}, false);


function checkVisibility(){
    
    
        if (isInViewport(words[0])) {
            
                document.getElementById("word5").style.animation='swift-text 0.6s forwards ease-in-out 0.0s';
                document.getElementById("word6").style.animation='swift-text 0.6s forwards ease-in-out 0.2s';
                document.getElementById("word7").style.animation='swift-text 0.6s forwards ease-in-out 0.4s';
                document.getElementById("word8").style.animation='swift-text 0.6s forwards ease-in-out 0.6s';
                document.getElementById("word9").style.animation='swift-text 0.6s forwards ease-in-out 0.8s';
                document.getElementById("word10").style.animation='swift-ptext 0.4s forwards ease-in-out 0.9s';
                document.getElementById("word11").style.animation='swift-ptext 0.4s forwards ease-in-out 1.0s';
                document.getElementById("word12").style.animation='swift-ptext 0.4s forwards ease-in-out 1.1s';
                document.getElementById("word13").style.animation='swift-ptext 0.4s forwards ease-in-out 1.2s';
        }
}


var isInViewport = function (elem) {
    var bounding = elem.getBoundingClientRect();
    return (
        bounding.top >= 0 &&
        bounding.left >= 0 &&
        bounding.top <= (window.innerHeight || document.documentElement.clientHeight) &&
        bounding.right-10 <= (window.innerWidth || document.documentElement.clientWidth)
    );
};


